.App {
  text-align: center;
  text-align: -webkit-center;
}
.file-uploader {
  width: 300px;
  height: 150px;
  border: 2px solid #003A9B;
  border-radius: 8px;
  padding: 10px;
  font-size: 16px;
}

.file-uploader--dragging {
  border-color: #9d2b6b;
}

.file-uploader__contents {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.file-uploader__file-name {
  font-weight: 700;
}

.pictureBox {
  min-height: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: nowrap;
  padding-top: 50px;
}
@media (max-width: 712px) {
  .pictureBox{
    flex-direction: column;
  }
}
.inputBox, .outputBox {
  border: 2px solid #2d2d2d;
  width: 100%;
  min-height: inherit;
}
.inputBox > img, .outputBox > img  {
  width: 100%;
}
.inputBox > div, .outputBox > div  {
  font-size: 1.5em;
  font-family: Helvetica, sans-serif;
  color: #003A9B;
  font-weight: bold;
  margin-top: 50px;
}
.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #003a9b;
  background: -moz-linear-gradient(left, #003a9b 10%, rgba(255, 255, 255, 0) 42%);
  background: -webkit-linear-gradient(left, #003a9b 10%, rgba(255, 255, 255, 0) 42%);
  background: -o-linear-gradient(left, #003a9b 10%, rgba(255, 255, 255, 0) 42%);
  background: -ms-linear-gradient(left, #003a9b 10%, rgba(255, 255, 255, 0) 42%);
  background: linear-gradient(to right, #003a9b 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #003a9b;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
